import { Controller } from '@ali/4ever-cangjie';
import { ITableSelection } from '../types';
import Table from '../../mo/models';

const getDataTableSelectionByTable = (
  controller: Controller,
  table: Table,
): ITableSelection | null => {
  const tableSelection = controller.query('tableSelection');
  if (!tableSelection) {
    return null;
  }
  const sameOrigin = controller.query(
    'isTableFromSameOrigin',
    {
      node: table,
      tableKey: tableSelection.key,
    },
  );
  if (sameOrigin) {
    return tableSelection;
  }
  return null;
};

export default getDataTableSelectionByTable;
